<template>
  <div class="home">
    <form action="" @submit.prevent="submit">
      <div class="header" style="margin-top: .6rem;">
        <div><img src="@/assets/img/logo.png" alt=""></div>
        <!-- <div><h2>科天云</h2></div> -->
      </div>
      <div class="content">
          <div class="wrap">
            <i class="iconfont">&#xe60b;</i>
            <input class="wrap-input-com" type="text"
            @blur.prevent="com" ref="comRef"
            v-model="inputText.com" placeholder="请输入站点名称"/>
            <label for="">.webex.com.cn</label>
          </div>
          <div class="wrap">
            <i class="iconfont">&#xe636;</i>
            <input class="wrap-input" type="text"
            @blur.prevent="username" ref="userRef"
            v-model="inputText.username" placeholder="请输入账号"/>
          </div>
          <div class="wrap">
            <i class="iconfont">&#xe678;</i>
            <input class="wrap-input" type="password"
            @blur.prevent="password" ref="passwordRef"
            v-model="inputText.password" placeholder="请输入密码"/>
            <i @click="see" class="iconfont see" v-show="!endShow">&#xe607;</i>
            <i @click="see" class="iconfont see" v-show="!openShow">&#xe625;</i>
          </div>
      </div>
      <div id="sliderOne">
        <huakuai ref="dataslider" id="a"></huakuai>
      </div>
      <div class="remember">
        <input type="checkbox" v-model="Recheckbox" >
        <label for="">记住账号密码</label>
      </div>
      <div class="footer">
        <button type="submit">登陆</button>
      </div>
    </form>
    <div class="bg-bottom" ref="bgBottom">
      <img src="@/assets/img/bottom.png" alt="">
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import Huakuai from '@/components/huakuai'
export default {
  name: 'Login',
  components: {
    Huakuai
  },
  data () {
    return {
      inputText: {
        com: Cookies.get('com'),
        username: Cookies.get('username'),
        password: Cookies.get('password')
      },
      Recheckbox: Cookies.get('Recheckbox'),
      endShow: true,
      openShow: false
    }
  },
  methods: {
    checkbox () {
      if (this.Recheckbox === false) {
        Cookies.remove('com')
        Cookies.remove('username')
        Cookies.remove('password')
        Cookies.remove('Recheckbox')
      } else {
        this.inputText.username = Cookies.get('username')
        this.inputText.com = Cookies.get('com')
        this.inputText.password = Cookies.get('password')
        this.Recheckbox = Cookies.get('Recheckbox')
      }
    },
    com () {
      const com = this.$refs.comRef
      let val = com.value
      const mobile = /[a-zA-Z]/.test(val)
      if (!mobile) {
        this.$layer.msg('请输入站点名称')
      } else {
      }
    },
    username () {
      const name = this.$refs.userRef
      let val = name.value
      // const mobile = /^[\u0391-\uFFE5]+$/.test(val)
      if (val.length <= 0) {
        this.$layer.msg('请输入账号')
      } else {
      }
    },
    password () {
      const password = this.$refs.passwordRef
      let val = password.value
      const mobile = /[^\u4e00-\u9fa5]/.test(val)
      if (!mobile) { this.$layer.msg('请输入密码') } else {
      }
    },
    see () {
      if (this.$refs.passwordRef.type === 'password') {
        this.openShow = true
        this.endShow = false
        this.$refs.passwordRef.type = 'text'
      } else {
        this.openShow = false
        this.endShow = true
        this.$refs.passwordRef.type = 'password'
      }
    },
    submit () {
      let com = this.$refs.comRef
      let name = this.$refs.userRef
      let password = this.$refs.passwordRef
      let hua = document.getElementById('silder2').getAttribute('data-huakuai')
      Cookies.set('com', this.inputText.com, { expires: 365 })
      Cookies.set('username', this.inputText.username, { expires: 365 })
      Cookies.set('password', this.inputText.password, { expires: 365 })
      Cookies.set('Recheckbox', this.Recheckbox, { expires: 365 })
      if (com.value.length <= 0) {
        com.focus()
        return false
      } else if (name.value.length <= 0) {
        name.focus()
        return false
      } else if (password.value.length <= 0) {
        password.focus()
        return false
      } else if (hua === 'false') {
        this.$layer.msg('拖动滑块验证')
      } else {
        this.postHandleA()
        this.checkbox()
      }
    },
    postHandleA () {
      const token = Cookies.get('name')
      axios({
        method: 'post',
        url: '/api/5b432334b00c9',
        headers: {'version': 'v3.0', 'Access-Control-Allow-Origin': '*', 'access-token': token},
        data: {
          webExId: this.inputText.username,
          password: this.inputText.password,
          siteName: this.inputText.com
        },
        responseType: 'json'
      }).then(this.Handle)
    },
    Handle (res) {
      const data = res.data
      if (data.code === -996) {
        if (this.PublicNum.ber <= 3) {
          this.funcName()
          this.postHandleA()
          // this.$layer.msg(res.data.msg)
        } else {
          this.$layer.msg('网络链接错误')
        }
      } else {
        this.PublicNum.endnumber()
        if (data.code === 1) {
          this.$layer.msg('登陆成功')
          setInterval(() => { this.$router.push('List') }, 500)
          return true
        } else if (data.code === -7) {
          this.$layer.msg('账号密码是否正确')
          return false
        } else {
          this.$layer.msg(res.data.msg)
        }
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .wrap >>> .tip
    display block
    width 100%
    font-size 12px
    text-align left
    margin-top .1rem
    color red
  .home
    text-align center
    .header
      h2
        font-size .5rem
    .content
      margin-top .6rem
      width 100%
      .wrap
        position relative
        display flex
        width 80%
        margin .3rem auto
        .wrap-input-com
          flex 1
          height .6rem
          color #666
          text-indent .4rem
          border-bottom 1px solid #1C9CF5
        label
          display inline-block
          line-height .6rem
        .wrap-input
          width 100%
          height .6rem
          font-size .26rem
          color #666
          text-indent .4rem
          border-bottom 1px solid #1C9CF5
        .iconfont
          display inline-block
          position absolute
          left 0
          top .1rem
        .see
          position absolute
          right 0
          left 90%
          width .4rem
          height .4rem
        span
          display block
          position absolute
          top .6rem
      .forget
        width 60%
        margin auto
        text-align right
    .footer
      margin-top .6rem
      width 100%
      button
        width 80%
        margin auto
        height .7rem
        font-size .34rem
        color #fff
        border 1px solid #fff
        cursor pointer
        background -webkit-linear-gradient(left, #0882FD  , #71BCF5) /* Safari 5.1 - 6.0 */
        background -o-linear-gradient(right, #0882FD , #71BCF5) /* Opera 11.1 - 12.0 */
        background -moz-linear-gradient(right, #0882FD , #71BCF5) /* Firefox 3.6 - 15 */
        background linear-gradient(to right, #0882FD  , #71BCF5) /* 标准的语法 */
    .bg-bottom
      width 100%
      position absolute
      bottom 0
      height 1.58rem
      img
        width 100%
        height 100%
    #sliderOne
      margin-top .48rem
    .remember
      width 80%
      margin .4rem auto
      text-align right
      label
        font-size .26rem
        color #666
      input
        width .3rem
        height .3rem
@media (max-height: 400px) {
  .bg-bottom {
    display: none;
  }
}
</style>



