<template>
  <div class="tips" >
    <mt-navbar v-model="selected">
      <mt-tab-item id="1">未开始({{List.length}})</mt-tab-item>
      <mt-tab-item id="2" >进行中({{OngoingList.length}})</mt-tab-item>
      <!-- <mt-tab-item id="3">已结束({{FinishedList.length}})</mt-tab-item> -->
    </mt-navbar>
    <!-- tab-container -->
    <mt-tab-container v-model="selected" >
      <mt-tab-container-item id="1">
        <div v-for="(item,index) of List" :key="item.index" class="started" @click="list(item.meetingkey)">
          <div class="header border-bottom">
            <span>会议名称:&nbsp;{{item.meetingname}}</span>
            <el-button class="btn" @click.stop="cancel(item.meetingkey, index)">X</el-button>
          </div>
          <div class="content">
            <span>会议未开始</span>
          </div>
          <div class="content">
            开始时间&nbsp;{{item.starttime}}
          </div>
          <div class="content">
            结束时间&nbsp;{{item.stoptime}}
          </div>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <div v-for="(item,index) of OngoingList" :key="item.index" class="started" @click="list(item.meetingkey)">
          <div class="header border-bottom">
            <span>会议名称:&nbsp;{{item.meetingname}}</span>
          </div>
          <div class="content">
            <span>会议进行中</span>
          </div>
          <div class="content">
            开始时间&nbsp;{{item.starttime}}
          </div>
          <div class="content">
            结束时间&nbsp;{{item.stoptime}}
          </div>
        </div>
      </mt-tab-container-item>
      <!-- <mt-tab-container-item id="3">
      	<div class="Lately" style="margin-top: 35px">
      		<div @click="LatelyThree">
      			<span>最近三天</span>
      			<span v-show="iconShow" class="iconfont">&#xe600;</span>
      			<span v-show="!iconShow" class="iconfont">&#xe601;</span>
      		</div>
      		<div class="started" v-show="!iconShow">
      			<div class="header border-bottom">
	            <span>会议名称:测试</span>
	          </div>
	          <div class="content">
	            <span>会议已结束</span>
	          </div>
	          <div class="content">
	            <span>开始时间</span>
	          </div>
	          <div class="content">
	            <span>结束时间</span>
	          </div>
	        </div>
	        <div class="started" v-show="!iconShow">
      			<div class="header border-bottom">
	            <span>会议名称:测试</span>
	          </div>
	          <div class="content">
	            <span>会议已结束</span>
	          </div>
	          <div class="content">
	            <span>开始时间</span>
	          </div>
	          <div class="content">
	            <span>结束时间</span>
	          </div>
	        </div>
      	</div>
      	<div class="Lately">
      		<span>最近一周</span><span class="iconfont">&#xe600;</span>
      	</div>
      	<div class="Lately">
      		<span>最近一个月</span><span class="iconfont">&#xe600;</span>
      	</div>
      </mt-tab-container-item> -->
    </mt-tab-container>
    <div class="btn">
      <router-link to="Appointments">
        <button @click="jump">预约</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
export default {
  name: 'List',
  data () {
    return {
      List: [],
      OngoingList: [],
      FinishedList: [],
      changeActive: 0,
      selected: '1',
      iconShow: true
    }
  },
  methods: {
  	LatelyThree () {
  		this.iconShow = !this.iconShow
  	},
    list (i) {
      this.$router.push({path: 'Details', query: {id: i}})
      //  this.$router.go(0)
      location.reload()
    },
    cancel (i, index) {
      this.$confirm(' 是否取消会议?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const token = Cookies.get('name')
        const NewTime = new Date().getTime()
        axios({
          method: 'post',
          url: '/api/5b4edb4f60ff3?t=' + NewTime,
          headers: {'version': 'v3.0', 'access-token': token},
          data: {
            meetingKey: i
          },
          responseType: 'json'
        })
        this.$refs.started[index].style.display = 'none'
      }).catch(() => {})
    },
    active (i, index) {
      const token = Cookies.get('name')
      let date = new Date()
      let Y = date.getFullYear() + '-'
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      let D = date.getDate() + ' '
      let h = date.getHours() + ':'
      let m = date.getMinutes() + ':'
      let s = date.getSeconds()
      const NewTime = Y + M + D + h + m + s
      axios({
        method: 'post',
        url: '/api/5b4edb4f60ff3?t=' + NewTime,
        headers: {'version': 'v3.0', 'access-token': token},
        data: {
          meetingKey: i
        },
        responseType: 'json'
      }).then(() => {
        if (this.$refs.started === undefined) {
          this.$refs.End[index].style.display = 'none'
          this.$layer.msg('已删除')
        } else {
          const star = this.$refs.started.length
          this.$refs.End[index - star].style.display = 'none'
          this.$layer.msg('已删除')
        }
      })
    },
    jump () {
      this.$router.push('Appointments')
      location.reload()
    },
    getToken () {
      const token = Cookies.get('name')
      const NewTime = new Date().getTime()
      axios({
        method: 'post',
        url: '/api/5b47103ab2ae5?t=' + NewTime,
        headers: {'version': 'v3.0', 'access-token': token},
        data: {
          'ConferenceStatus': 'Pending'
        },
        responseType: 'json'
      }).then(this.HandleClick)
    },
    HandleClick (res) {
      const data = res.data
      if (data.code === -996) {
        if (this.PublicNum.ber <= 3) {
          this.PublicNum.setname()
          this.funcName()
          this.getToken()
          this.$layer.msg(res.data.msg)
          return false
        } else {
          this.$layer.msg('网络链接错误')
        }
      } else {
        this.PublicNum.endnumber()
        if (data.code === 1) {
          this.List = data.data
          event.cancelBubble = true//  阻止事件冒泡
          return true
          //  this.$router.go(0)
        } else if (data.code === -7) {
          this.$layer.msg('请从新登陆')
          return false
        }
      }
    }
  },
  mounted () {
    this.getToken()
  },
  watch: {
    selected:{
      deep: true,
      handler: function (value) {
        const token = Cookies.get('name')
        const NewTime = new Date().getTime()
        if (value == 2) {
          axios({
            method: 'post',
            url: '/api/5b47103ab2ae5?t=' + NewTime,
            headers: {'version': 'v3.0', 'access-token': token},
            data: {
              'ConferenceStatus': 'Ongoing'
            },
            responseType: 'json' 
          }).then((res) => {
            const data = res.data
            if (data.code === 1) {
              this.OngoingList = data.data
            }
          })
        } else if (value == 3) {
          axios({
            method: 'post',
            url: '/api/5b47103ab2ae5?t=' + NewTime,
            headers: {'version': 'v3.0', 'access-token': token},
            data: {
              'ConferenceStatus': 'Finished'
            },
            responseType: 'json' 
          }).then((res) => {
            const data = res.data
            if (data.code === 1) {
              this.FinishedList = data.data
            }
          })
        }
      }
      
    }
  }
}
</script>

<style lang="stylus" scoped>
  .tips >>> .active
    display none
  @import "~style/input.styl"
  .tips >>> .mint-tab-item-label
    font-size .3rem
  .tips >>> .mint-navbar .mint-tab-item.is-selected
    margin-bottom 0
    border-bottom 2px solid #26a2ff
  .tips
  	display flex
    margin-bottom 1.6rem
  .started
    text-align center
    padding-bottom .4rem
    HeaderT()
    .header
      ColorHeander()
      height 1.2rem
      padding .3rem 0
      margin .2rem 0
      span
        display block
        width 100%
        overflow: hidden
        text-align center
        text-overflow ellipsis
        white-space nowrap
    .content
      Cont()
      span
        color #333
      button
        font-size .3rem
        width .4rem
        height .4rem
        line-height 1
        margin-top -.14rem
        background #1C9CF5
        color #fff
        border none
        font-weight bold
        border-radius 50%
        padding .08rem
  .end
    text-align center
    HeaderT()
    .header
      display flex
      ColorHeander()
      height 1.2rem
      padding .3rem 0
      margin .2rem 0
      color #999
      span
        width 100%
        display inline-block
        text-overflow ellipsis
        white-space nowrap
      button
        position absolute
        top 0
        right .2rem
        background #fff
        color #606266
        font-size .32rem
    .content
      Cont()
      color #999
      button
        font-size .3rem
        width .4rem
        height .4rem
        line-height 1
        margin-top -.14rem
        background #1C9CF5
        color #fff
        border none
        font-weight bold
        border-radius 50%
        padding .08rem
  .btn
    position absolute
    top 0.2rem
    right 0.2rem
    text-align center
    width .4rem
    height .4rem
    padding 0
    border none
    font-weight 700
    font-size .32rem
    button
      Btn()
      margin-top 6rem
  .tips .Lately
  	flex 1
  	margin 18px 20px
  	padding .2rem 0
  	border-radius 4px
  	box-shadow 0px 0 10px #eaeaea
  	span
  		display block
  		width 100%
  		padding-top 8px
  		text-align center
  		color #666
  		font-size .36rem
  	.iconfont
  	  padding-top 8px
</style>



