<template>
  <div class="page">
    <div class="silder1">拖动滑块验证</div>
    <div class="silder2" id="silder2" ref="span" :style="touth" data-huakuai="false">
      <strong v-show="!Show">验证成功</strong>
      <span class="box"
      ref="box"
      :style="jinzi"
      @touchstart="touchStart"
      @touchmove="touchMove"
      @touchend="touchEnd">
        <i class="i"><img src="@/assets/img/slider.png" alt=""></i>
      </span>
      <i class="i show" v-show="!Show"><img src="@/assets/img/end.png" alt=""></i>
    </div>
  </div>
</template>
<script>
export default{
  name: 'Huakuai',
  data () {
    return {
      startX:0,//开始触摸的位置
      moveX:0,//滑动时的位置
      endX:0,//结束触摸的位置
      disX:0,
      touth: '',
      jinzi: '',
      Show: 'true'
    }
  },
  methods: {
    touchStart (ev) {
      ev = ev || event
      ev.preventDefault()
      if(ev.touches.length === 1) {//tounches类数组，等于1时表示此时有只有一只手指在触摸屏幕
        this.startX = ev.touches[0].clientX
      }
    },
    touchMove (ev) {
      ev = ev || event
      ev.preventDefault()
      if (ev.touches.length === 1) {
        this.moveX = ev.touches[0].clientX
        this.disX = this.moveX - this.startX
        this.touth = 'transform: translateX(' + this.disX + 'px' + ')'
      }
    },
    touchEnd (ev) {
      ev = ev || event
      ev.preventDefault()
      this.endX = ev.changedTouches[0].clientX
      let width = this.$refs.span.offsetWidth - 30
      if (this.endX < width) {
        this.touth = 0
      } else {
        this.touth = 'transform: translateX(' + width + 'px' + ')'
        this.Show = false
        this.$refs.span.dataset.huakuai = true
      }
    }

  }
}
</script>
<style lang="stylus" scoped>
  .page
    text-align center
    margin .3rem 0
    width 5rem
    margin 0 auto
    position relative
    overflow hidden
    .silder1
      width 5rem
      height .6rem
      line-height .6rem
      text-align center
      font-size .28rem
      background #eee
      overflow hidden
    .silder2
      position absolute
      top 0
      left -4.4rem
      width 100%
      height .8rem
      background #09BB07
      .box,.i
        position absolute
        right 0
        display block
        width .6rem
        height .6rem
        background #fff
        img
          width 100%
          height 100%
          padding .2rem
      .show
        background #eee
        box-shadow 1px 0px 15px #fff
      strong
        display block
        position absolute
        left 0
        right 0
        top 0
        font-size .28rem
        color #fff
        line-height .6rem
        text-indent -1rem
</style>



