<template  >
  <div class="content" >
    <form action="" @submit.prevent="submit" >
      <div class="form-warp">
        <input class="Input" type="text" placeholder="会议名称" v-model="meeting" ref="meetings"/>
        <i class="iconfont">&#xe75f;</i>
      </div>
      <div class="form-warp">
        <input class="Input" @click.stop="NewDate" type="text" placeholder="开始日期" ref="StartTime" v-model="DataMM" readonly/>
        <i class="iconfont">&#xe620;</i>
        <mt-datetime-picker
          v-model="pickerData"
          ref="Date"
          type="datetime"
          :startDate="KDate"
          @confirm="Dateconfirm"
          month-format="{value}月"
          date-format="{value}日"
          hour-format="{value}时"
          minute-format="{value}分">
        </mt-datetime-picker>
      </div>
      <!-- <div class="form-warp time">
        <input class="Input" @click.stop="openPickerDatetime" type="text" placeholder="开始日期" ref="StartTime" v-model="dataEnd" readonly/>
        <mt-popup v-model="pickerDataTime" position="bottom" ref="pickerdatetime">
          <div class="picker-toolbar">  
            <span class="mint-datetime-cancel" @click="cancle">取消</span>  
            <span class="mint-datetime-confirm" @click="select">确定</span>  
          </div>
          <mt-picker :slots="slotsdatatime" @change="onValuesDateTimeChange">
          </mt-picker>
        </mt-popup>
        <i class="iconfont">&#xe620;</i>
      </div> --> 
      <div class="form-warp time">
        <input class="Input" @click.stop="openPicker" type="text" placeholder="持续时间" ref="Endime" v-model="dataEnd" readonly/>
        <mt-popup v-model="pickerValue" position="bottom" ref="picker">
        	<div class="picker-toolbar">  
            <span class="mint-datetime-cancel" @click="cancle">取消</span>  
            <span class="mint-datetime-confirm" @click="select">确定</span>  
        	</div>
        	<mt-picker :slots="slots" @change="onValuesChange">
        	</mt-picker>
        </mt-popup>
        <i class="iconfont">&#xe67f;</i>
      </div>
      <div style="height: 150px"></div>
      <button type="submit">提交</button>
    </form>
  </div>
</template>

<script>
import 'style/btnIcon.css'
import axios from 'axios'
import Cookies from 'js-cookie'
export default {
  name: 'Appointments',
  data () {
    return {
      pickerData: new Date(new Date().getTime() + 1000 * 60 * 3),
      pickerDataTime: false,
      pickerValue: false,
      KDate: new Date(new Date().getTime() + 1000 * 60 * 3),
      Time: '',
      MM: '',
      DataMM: '',
      meeting: '',
      dataStart: '',
      dataEnd: '',
      dataLanguage: '',
      dataPassword: '',
      NewDates: '',//当前的日期信息
      yearNow: '',//当前的年数
      monthNow: '',//当前的月份
      dayNow: '',
      input: {
        rtm: false,
        crm: false,
        Password: ''
      },
      options: [{
        value: '001',
        label: '仅使用网络语音'
      }],
      show: false,
      slots: [
        {
          flex: 1,
          values: ['01小时', '02小时', '03小时', '04小时', '05小时', '06小时', '07小时', '08小时', '09小时', '10小时', '11小时', '12小时', '13小时', '14小时', '15小时', '16小时', '17小时', '18小时', '19小时', '20小时', '21小时', '22小时', '23小时'],
          className: 'slot1',
          textAlign: 'left'
        }, {
          divider: false,
          content: ':',
          className: 'slot2'
        }, {
          flex: 1,
          values: ['0分钟', '15分钟', '30分钟', '45分钟', '59分钟'],
          className: 'slot3',
          textAlign: 'right'
        }
      ],
      slotsdatatime: [
        {
          flex: 1,
          values: ['2018年', '2019年', '2020年'],
          className: 'slot1',
          textAlign: 'left'
        }, {
          flex: 1,
          values: ['1月', '2月', '3月'],
          className: 'slot2',
          textAlign: 'right'
        }, {
          flex: 1,
          values: ['1日', '2日', '3日', '4日'],
          className: 'slot3',
          textAlign: 'right'
        }, {
          flex: 1,
          values: ['1时', '2时', '3时'],
          className: 'slot4',
          textAlign: 'right'
        }, {
          flex: 1,
          values: ['0分', '5分', '10分'],
          className: 'slot5',
          textAlign: 'right'
        }
      ]
    }
  },
  methods: {
  	cancle () {
  		this.pickerValue = false
  		this.dataEnd = ''
  	},
  	select () {
  		this.pickerValue = false
  		this.dataEnd = this.Time
  	},
    // onValuesDateTimeChange (picker, values) {

    //   console.log(picker)
    //   alert(values)
    // },
  	onValuesChange (picker, values) {
  		this.Time = values[0] + values[2]
  		let a = values[0]
  		let b = parseInt((a[0] + a[1]) * 60)
  		let c = values[2]
  		let d = parseInt(c[0] + c[1])
  		this.MM = b + d
  		console.log(this.MM)
  	},
    NewDate () {
      this.$refs.Date.open()
    },
    Dateconfirm (value) {
      let date = new Date(value)
      this.DataMM =date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日' + date.getHours() + '时' + date.getMinutes() + '分钟'
      this.dataStart = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes()
    },
    openPicker () {
      this.$refs.picker.open()
    },
    openPickerDatetime () {
      this.$refs.pickerdatetime.open()
    },
    confirm (value) {
      let a = value
      this.dataEnd = value[0] + value[1] + '小时' + value[3] + value[4] + '分钟'
      let b = parseInt((a[0] + a[1]) * 60)
      let c = parseInt(a[3] + a[4])
      this.MM = b + c
    },
    Handledata () {
      const token = Cookies.get('name')
      let date = new Date()
      let Y = date.getFullYear() + '-'
      let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      let D = date.getDate() + ' '
      let h = date.getHours() + ':'
      let m = date.getMinutes() + ':'
      let s = date.getSeconds()
      const NewTime = Y + M + D + h + m + s
      axios({
        method: 'post',
        url: '/api/5b1a190c62cb9?t=' + NewTime,
        headers: {'version': 'v3.0', 'access-token': token},
        data: {
          meetingName: this.meeting,
          meetingStart: this.dataStart,
          meetduration: this.MM,
          meetingPassword: this.input.Password
        }
      }).then(this.Handle)
    },
    Handle (res) {
      const data = res.data
      if (data.code === -996) {
        if (this.PublicNum.ber <= 3) {
          this.PublicNum.setname()
          this.funcName()
          this.Handledata()
          this.$layer.msg(res.data.msg)
        } else {
          this.$layer.msg('网络链接错误')
        }
      } else {
        this.PublicNum.endnumber()
        if (data.code === 1) {
          let key = ''
          key = res.data.data.meetingkey
          setInterval(() => { this.$router.push({path: 'Details', query: {id: key}}) }, 400)
        } else if (data.code === -7) {
          this.$layer.msg('请从新登陆')
          return false
        }
      }
    },
    BlurPassword () {
      let length = this.$refs.password.value.length
      if (length < 4 || length > 16) {
        this.$layer.msg('密码至少4位最多16位不能为汉字')
      }
    },
    submit () {
      let meetings = this.$refs.meetings
      let start = this.$refs.StartTime
      let endime = this.$refs.Endime
      // let select = this.$refs.select
      if (meetings.value === '') {
        meetings.focus()
        return false
      } else if (start.value === '') {
        start.focus()
        return false
      } else if (endime.value === '') {
        endime.focus()
        return false
      } else {
        this.$dialog.alert({
          title: '提交完成',
          message: '您的邮箱将会收到会议信息，请查收',
          confirmButtonText: '知道了'
        }).then(() => {
          this.Handledata()
        }).catch(() => {
          // on cancel
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~style/input.styl"
  @import "~style/var.styl"
  
  .time >>> .mint-popup.mint-popup-bottom
  	left 0
  	right 0
  	transform translate(0, 0)
  	.picker-toolbar span
  		padding 5px
  		font-size .4rem
  .form-warp
    position relative
    width 92%
    margin .6rem 4%
    height $Height
    line-height $Height
  .form-warp  .Input
      width 100%
      height 100%
      line-height $Height
      background #eee
      font-size .26rem
      text-indent .68rem
      border-radius .8rem
  .form-warp .Input::-webkit-input-placeholder
    color #C0C4CC
  .form-warp .Password::-webkit-input-placeholder
    color #0882fd
  .form-warp  i
      position absolute
      display inline-block
      left .178rem
      top .03rem
    button
      Btn()
    .btnicon
      display flex
      justify-content center
      width 92%
      margin auto
      z-index 15
    .Icontext
      width 92%
      display flex
      margin auto
      p
        width 100%
        text-align center
        span
          display inline-block
          width 70px
          font-size .28rem
          color #333
          font-weight bold
          margin 5px 20px
.el-date-editor >>> .el-input__inner
    width 90%
    height .78rem
    line-height .78rem
    border none
    background #eee
    color #333
    border-radius .8rem
    padding 0
.el-date-editor >>> .el-input__icon
  display none
.form-warp >>> .el-select
  width 100%
.el-select >>> .el-input__inner
  border-color transparent
  text-indent .7rem
  padding 0
  font-size .26rem
  border-radius .8rem
  background #eee
</style>



