import Vue from 'vue'
import Router from 'vue-router'
import Loing from '@/components/login'
import List from '@/components/list'
import Appointments from '@/components/appointments'
import Details from '@/components/details'
import Huakuai from '@/components/huakuai'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Loing',
      component: Loing,
      meta: {
        keepAlive: false // 不需要缓存
      }
    }, {
      path: '/List',
      name: 'List',
      component: List,
      meta: {
        keepAlive: false // 不需要缓存
      }
    }, {
      path: '/Appointments',
      name: 'Appointments',
      component: Appointments,
      meta: {
        keepAlive: false // 不需要缓存
      }
    }, {
      path: '/Details',
      name: 'Details',
      component: Details,
      meta: {
        keepAlive: false // 不需要缓存
      }
    }, {
      path: '/Huakuai',
      name: 'Huakuai',
      component: Huakuai,
      meta: {
        keepAlive: false // 不需要缓存
      }
    }
  ]
})



